.playlist-song {
  border-bottom: 1px solid #7c7c86;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #7c7c86;
  cursor: pointer;
  font-size: 1.25em;
  max-width: 100%;
  overflow: hidden;
  padding: .7em 1.5em;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  -webkit-transition: color 100ms ease-in-out;
  -o-transition: color 100ms ease-in-out;
  transition: color 100ms ease-in-out;
  white-space: nowrap;
  width: 100%;
}

.playlist-song:hover {
  color: #aeadb4;
}

.playlist-song--selected {
  border-bottom: 1px solid #7c7c86;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: white;
  cursor: pointer;
  font-size: 1.25em;
  padding: .7em 1.5em;
  -webkit-transition: color 100ms ease-in-out;
  -o-transition: color 100ms ease-in-out;
  transition: color 100ms ease-in-out;
}

.playlist-song--selected:hover {
  color: white;
}



