.player-progress-bar {
  border: 1px solid white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 1em;
  margin: .2em 0;
  width: calc(100% 0 .26em);
}

.player-progress-current {
  background-color: white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: .65em;
  margin: .13em;
  -webkit-transition: -webkit-transform 1000ms cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: -webkit-transform 1000ms cubic-bezier(0.39, 0.575, 0.565, 1);
  -o-transition: transform 1000ms cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: transform 1000ms cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: transform 1000ms cubic-bezier(0.39, 0.575, 0.565, 1), -webkit-transform 1000ms cubic-bezier(0.39, 0.575, 0.565, 1);
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
  width: 100%;
}



